/* ========== Ultra Modern Base Styles ========== */
.th-login-admin-modern {
  --primary-color: #4361ee;
  --primary-hover: #3f37c9;
  --primary-light: rgba(67, 97, 238, 0.15);
  --error-color: #ff3e4d;
  --error-hover: #ff1a2e;
  --success-color: #00d9b8;
  --warning-color: #ffaa00;
  --text-color: #1a1e2e;
  --text-light: #6c7286;
  --border-color: rgba(237, 242, 244, 0.8);
  --bg-light: #f5f7fa;
  --bg-lighter: #ffffff;
  --white: #ffffff;
  --black: #0f111a;
  --shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 12px -2px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 12px 32px -4px rgba(0, 0, 0, 0.12);
  --transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-fast: all 0.15s ease-out;
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.2);
  --glass-blur: 16px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  color: var(--text-color);
  background: var(--bg-light);
  min-height: 100vh;
  line-height: 1.6;
}

@supports (font-variation-settings: normal) {
  .th-login-admin-modern {
    font-family: 'Inter var', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  }
}

/* ========== Glassmorphism Header ========== */
.admin-header {
  background: var(--glass-bg);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
  box-shadow: var(--shadow-sm);
  padding: 0 24px;
  border-bottom: 1px solid var(--glass-border);
  position: sticky;
  top: 0;
  z-index: 1000;
  transition: var(--transition);
}

.admin-header:hover {
  background: rgba(255, 255, 255, 0.92);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.header-content {
  max-width: 1800px;
  margin: 0 auto;
  padding: 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
}

.header-content h2 {
  font-size: 24px;
  font-weight: 700;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 12px;
  letter-spacing: -0.5px;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  -webkit-background-clip: text;
  background-clip: text;
}

.th-logo {
  display: inline-flex;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  color: var(--white, #fff) !important;
  width: 36px;
  height: 36px;
  border-radius: var(--radius-sm);
  align-items: center;
  justify-content: center;
  font-size:20px;
  font-weight: 700;
  box-shadow: 0 4px 12px rgba(67, 97, 238, 0.3);
  transition: var(--transition);
}

/* ========== Floating Notice Banner ========== */
.notice-banner {
  padding: 12px 20px;
  border-radius: var(--radius-sm);
  font-size: 14px;
  flex: 1;
  min-width: 240px;
  margin-left: 20px;
  box-shadow: var(--shadow-sm);
  transform: translateY(-2px);
  transition: var(--transition-fast);
  border-left: none;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.notice-banner::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: currentColor;
  opacity: 0.8;
}

.notice-banner.success {
  background-color: rgba(0, 217, 184, 0.12);
  color: var(--success-color);
}

.notice-banner.error {
  background-color: rgba(255, 62, 77, 0.12);
  color: var(--error-color);
}

.notice-banner.warning {
  background-color: rgba(255, 170, 0, 0.12);
  color: var(--warning-color);
}

.notice-banner:hover {
  transform: translateY(0);
  box-shadow: var(--shadow-md);
}

/* ========== Neomorphic Glass Sidebar ========== */
.admin-container {
  display: flex;
  max-width: 1800px;
  margin: 0 auto;
  gap: 24px;
  padding: 24px;
}

.admin-sidebar {
  width: 280px;
  background: var(--glass-bg);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--glass-border);
  padding: 16px 0;
  align-self: flex-start;
  position: sticky;
  top: 88px;
  max-height: calc(100vh - 112px);
  overflow-y: auto;
  transition: var(--transition);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
}

.admin-sidebar:hover {
  box-shadow: var(--shadow-lg);
  border-color: rgba(255, 255, 255, 0.3);
}

.admin-content {
  flex: 1;
  min-width: 0;
}

/* ========== 3D Glass Navigation ========== */
.admin-sidebar ul {
  list-style: none;
  margin: 0;
  padding: 8px;
}

.admin-sidebar li {
  margin: 4px 0;
  position: relative;
}

.admin-sidebar li button {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 12px 20px;
  color: var(--text-light);
  text-decoration: none;
  transition: var(--transition-fast);
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  gap: 12px;
  border-radius: var(--radius-sm);
  font-weight: 500;
  backdrop-filter: blur(4px);
}

.admin-sidebar li button:hover {
  background: var(--primary-light);
  color: var(--primary-color);
  transform: translateX(4px);
  box-shadow: 2px 0 8px rgba(67, 97, 238, 0.1);
}

.admin-sidebar li.active button {
  background: var(--primary-light);
  color: var(--primary-color);
  font-weight: 600;
}



.admin-sidebar .dashicons {
  font-size: 18px;
  width: 24px;
  height: 24px;
  transition: var(--transition-fast);
  position: relative;
}

.admin-sidebar .dashicons:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.admin-sidebar li:hover .dashicons {
  transform: scale(1.1);
}

/* ========== Glass Card Components ========== */
.settings-section {
  margin-bottom: 32px;
}

.settings-card {
  background: var(--glass-bg);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  border: 1px solid var(--glass-border);
  padding: 28px;
  margin-bottom: 24px;
  transition: var(--transition);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
}

.section-title {
  font-size: 20px;
  font-weight: 700;
  margin: 0 0 24px 0;
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-color);
  position: relative;
  padding-bottom: 12px;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 48px;
  height: 3px;
  background: linear-gradient(90deg, var(--primary-color), rgba(67, 97, 238, 0.5));
  border-radius: 3px;
  box-shadow: 0 2px 4px rgba(67, 97, 238, 0.2);
}

.section-title .dashicons::before{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.section-title .dashicons {
  font-size: 24px;
  color: var(--primary-color);
  background: var(--primary-light);
  padding: 8px;
  border-radius: 50%;
  box-shadow: 0 2px 8px rgba(67, 97, 238, 0.1);
  position: relative;
}

/* ========== Animated Glass Setting Rows ========== */
.setting-row {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid rgba(237, 242, 244, 0.6);
  gap: 24px;
  transition: var(--transition-fast);
  position: relative;
  padding-left:10px;
}

.setting-row::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(67, 97, 238, 0.2), transparent);
  opacity: 0;
  transition: var(--transition-fast);
}

.setting-row:hover::after {
  opacity: 1;
}

.setting-label {
  flex: 1;
  min-width: 240px;
}

.setting-label h3, 
.setting-label h4 {
  margin: 0 0 6px 0;
  font-size: 15px;
  font-weight: 600;
  color: var(--text-color);
}

.setting-label .description {
  margin: 0;
  color: var(--text-light);
  font-size: 13px;
  line-height: 1.5;
}

.setting-control {
  flex: 1;
  min-width: 200px;
  max-width: 200px;
}

/* ========== Glass Card Groups ========== */
.settings-group {
  margin: 22px 0;
}

.group-title {
  font-size: 17px;
  margin: 0 0 16px 0;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(237, 242, 244, 0.8);
  color: var(--text-color);
  display: flex;
  align-items: center;
  gap: 8px;
}

.group-title .dashicons {
  font-size: 20px;
  background: rgba(67, 97, 238, 0.1);
  padding: 6px;
  border-radius: 6px;
  color: var(--primary-color);
}

.group-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  flex-wrap: wrap;
  gap: 12px;
}

/* ========== Grid Layout with Glass Cards ========== */
.redirection-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
  margin-top: 16px;
}

.redirection-card {
  background: var(--glass-bg);
  border-radius: var(--radius-md);
  padding: 20px;
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow-sm);
  transition: var(--transition);
}

.redirection-card h4 {
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 15px;
  color: var(--text-color);
  display: flex;
  align-items: center;
  gap: 8px;
}

.redirection-card h4 .dashicons {
  color: var(--primary-color);
  background: var(--primary-light);
  padding: 4px;
  border-radius: 4px;
}

/* ========== Role Redirect Glass Cards ========== */
.role-redirects-container {
  margin-top: 16px;
  display: grid;
  gap: 16px;
}

.redirect-rule {
  background: var(--glass-bg);
  border-radius: var(--radius-md);
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
  transition: var(--transition);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.redirect-rule:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(255, 255, 255, 0.4);
}

.rule-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 20px;
  background: rgba(245, 247, 250, 0.6);
  border-bottom: 1px solid var(--glass-border);
}

.rule-header h4 {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-color);
  display: flex;
  align-items: center;
  gap: 8px;
}

.rule-header h4 .dashicons {
  color: var(--primary-color);
}

.remove-rule {
  background: none;
  border: none;
  color: var(--text-light);
  cursor: pointer;
  padding: 6px;
  transition: var(--transition-fast);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(4px);
}

.remove-rule:hover {
  color: var(--error-color);
  background: rgba(255, 62, 77, 0.1);
  transform: scale(1.1);
}

.rule-fields {
  padding: 20px;
  display: grid;
  gap: 16px;
}

/* ========== Premium Glass Form Fields ========== */
.form-field {
  margin-bottom: 16px;
  position: relative;
}

.form-field label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 13px;
  color: var(--text-color);
}

.form-field input[type="text"],
.form-field input[type="email"],
.form-field input[type="url"],
.form-field input[type="number"],
.form-field select,
.form-field textarea {
  width: 100%;
  padding: 10px 14px;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  background: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  transition: var(--transition-fast);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.03);
  backdrop-filter: blur(4px);
}

.form-field input[type="text"]:focus,
.form-field input[type="email"]:focus,
.form-field input[type="url"]:focus,
.form-field input[type="number"]:focus,
.form-field select:focus,
.form-field textarea:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px var(--primary-light);
  outline: none;
  background: var(--white);
}

.form-field textarea {
  min-height: 120px;
  resize: vertical;
}

.help-text {
  font-size: 12px;
  color: var(--text-light);
  margin: 6px 0 0;
  line-height: 1.5;
}

/* ========== Floating Glass Action Buttons ========== */
.add-rule-button {
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  color: var(--white);
  border: none;
  border-radius: var(--radius-sm);
  padding: 10px 18px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: var(--transition);
  box-shadow: 0 4px 12px rgba(67, 97, 238, 0.3);
  position: relative;
  overflow: hidden;
}

.add-rule-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), transparent);
  opacity: 0;
  transition: var(--transition-fast);
}

.add-rule-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(67, 97, 238, 0.4);
}

.add-rule-button:hover::before {
  opacity: 1;
}

.add-rule-button:active {
  transform: translateY(0);
}

.customizer-button {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  color: var(--white);
  text-decoration: none;
  padding: 12px 20px;
  border-radius: var(--radius-sm);
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 20px;
  transition: var(--transition);
  box-shadow: 0 4px 12px rgba(67, 97, 238, 0.3);
}

/* ========== Interactive Glass Design Preview ========== */
.design-settings-content {
  display: flex;
  gap: 32px;
  flex-wrap: wrap;
}

.design-preview {
  flex: 1;
  min-width: 320px;
  perspective: 1000px;
}

.preview-window {
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-md);
  overflow: hidden;
  background: var(--glass-bg);
  box-shadow: var(--shadow-md);
  transform-style: preserve-3d;
  transition: var(--transition);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.preview-window:hover {
  transform: rotateY(5deg) rotateX(2deg);
  box-shadow: var(--shadow-lg);
}

.preview-overlay {
  height: 240px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  background: linear-gradient(135deg, rgba(67, 97, 238, 0.05) 0%, rgba(255, 255, 255, 0.1) 100%);
}

/* Popup Modal */
.preview-overlay .preview-form {
  background: #fff;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 16px;
  transition: all 0.3s ease;
}

/* Sliding Panel Preview */
.layout-sliding .preview-form {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  border-radius: 0;
  border-left: 1px solid #ddd;
  padding: 24px;
  box-shadow: -6px 0 16px rgba(0, 0, 0, 0.08);
  background: #fff;
  animation: slideIn 0.3s ease forwards;
}

/* Sliding animation */
@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0%);
    opacity: 1;
  }
}

.preview-panel {
	padding: 40px;
	background: #f2f4f7;
	display: flex;
	align-items: center;
	justify-content: center;
	border-left: 1px solid var(--border-light);
	overflow: auto;
	position: relative;
}

.preview-overlay {
	width: 100%;
	max-width: 720px;
	height: 540px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	background-color: rgba(255, 255, 255, 0.45);
	box-shadow: 0 8px 20px rgba(0, 0, 0, 0.06);
	border: 1px solid var(--border-light);
	transition: all 0.4s ease;
}

/* Form Inputs and Buttons */
.preview-form input,
.preview-form button {
  padding: 10px 14px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.preview-form button {
  background: #0073aa;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.2s ease;
}

.preview-form button:hover {
  background: #005e8a;
}


.preview-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
	border-radius: 10px;
	background: #fff;
	position: relative;
	overflow: hidden;
	text-align: center;
	transition: all 0.3s ease;
}

.preview-form h3 {
  margin: 0 0 16px 0;
  color: var(--text-color);
  font-weight: 600;
}

.preview-form .preview-input {
  width: 80%;
  height: 36px;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  margin-bottom: 12px;
  background: rgba(255, 255, 255, 0.8);
}

.preview-form .preview-button {
  width: 80%;
  height: 36px;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  border-radius: var(--radius-sm);
  box-shadow: 0 2px 8px rgba(67, 97, 238, 0.3);
}

.design-controls {
  flex: 1;
  min-width: 320px;
}

.quick-color-controls {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 16px;
}

.color-picker {
  position: relative;
}

.color-picker input {
  padding-left: 48px !important;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.8) !important;
}

.color-picker::before {
  content: "";
  position: absolute;
  left: 19px;
  top: 61%;
  transform: translate(-50%, -50%);
  width: 21px;
  height: 21px;
  border-radius: var(--radius-sm);
  /* border: 1px solid var(--border-color); */
  background: conic-gradient(red, yellow, lime, aqua, blue, magenta, red);
  transition: var(--transition-fast);
}

/* ========== Animated Glass Save Button ========== */
.save-settings {
    text-align: right;
    margin-right: 14%;
}

.save-button {
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  color: var(--white);
  border: none;
  border-radius: var(--radius-sm);
  padding: 8px 11px;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 5px;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
}

.save-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: 0.5s;
}

.save-button:hover::before {
  left: 100%;
}

.save-button:active {
  transform: translateY(0);
}

.save-button .spinner {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: var(--white);
  animation: spin 0.8s linear infinite;
}

/* ========== Premium Glass Confirmation Modal ========== */
.confirmation-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 17, 26, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  animation: fadeIn 0.3s ease-out;
}

.modal-content {
  background: var(--glass-bg);
  border-radius: var(--radius-lg);
  padding: 32px;
  width: 100%;
  max-width: 520px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.2);
  transform-origin: center;
  animation: scaleIn 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid var(--glass-border);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
}

@keyframes scaleIn {
  from { opacity: 0; transform: scale(0.95); }
  to { opacity: 1; transform: scale(1); }
}

.modal-content h3 {
  margin-top: 0;
  margin-bottom: 16px;
  color: var(--text-color);
  font-size: 20px;
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--primary-hover) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.modal-content p {
  color: var(--text-light);
  margin-bottom: 0;
  line-height: 1.6;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 24px;
}

.cancel-button {
  background: rgba(245, 247, 250, 0.8);
  color: var(--text-color);
  border: none;
  border-radius: var(--radius-sm);
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid var(--glass-border);
}

.cancel-button:hover {
  background: rgba(237, 242, 244, 0.8);
  transform: translateY(-2px);
  box-shadow: var(--shadow-sm);
}

.confirm-button {
  background: linear-gradient(135deg, var(--error-color) 0%, var(--error-hover) 100%);
  color: var(--white);
  border: none;
  border-radius: var(--radius-sm);
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  box-shadow: 0 4px 12px rgba(239, 35, 60, 0.3);
}

.confirm-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(239, 35, 60, 0.4);
}

/* ========== Responsive Adjustments ========== */
@media (max-width: 1200px) {
  .admin-container {
    flex-direction: column;
  }
  
  .admin-sidebar {
    width: 100%;
    position: static;
    max-height: none;
    margin-bottom: 24px;
  }
  
  .admin-sidebar ul {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
  }
  
  .admin-sidebar li {
    flex: 1;
    min-width: 120px;
  }
  
  .admin-sidebar li button {
    justify-content: center;
    text-align: center;
    flex-direction: column;
    padding: 12px 8px;
  }
  
  .admin-sidebar li.active::before {
    display: none;
  }
  
  .design-settings-content {
    flex-direction: column;
  }
  
  .redirection-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .notice-banner {
    margin-left: 0;
    width: 100%;
  }
  
  .setting-row {
    flex-direction: column;
    gap: 12px;
    padding: 16px 0;
  }
  
  .setting-label,
  .setting-control {
    width: 100%;
    min-width: auto;
  }
  
  .modal-actions {
    flex-direction: column;
  }
  
  .modal-actions button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .admin-container {
    padding: 16px;
  }
  
  .settings-card {
    padding: 20px;
  }
  
  .section-title {
    font-size: 18px;
  }
  
  .quick-color-controls {
