  }
  
  .save-button {
    width: 100%;
    justify-content: center;
  }
}

/* ========== Micro-interactions ========== */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-6px); }
}

.floating {
  animation: float 3s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.03); }
}

.pulse {
  animation: pulse 2s ease-in-out infinite;
}

/* ========== Custom Scrollbar ========== */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(237, 242, 244, 0.5);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: rgba(108, 114, 134, 0.5);
  border-radius: 4px;
  transition: var(--transition-fast);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
  transform: scale(1.1);
}

.modal-content .components-button.is-destructive.confirm-button{
  color:white !important;
}

.th-login-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin: 20px 0;
}

.th-login-loader-circle {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #4a6cf7;
  animation: th-login-pulse 1.5s infinite ease-in-out;
}

.th-login-loader-circle:nth-child(1) {
  animation-delay: 0s;
}

.th-login-loader-circle:nth-child(2) {
  animation-delay: 0.2s;
}

.th-login-loader-circle:nth-child(3) {
  animation-delay: 0.4s;
}

.th-login-loader-circle:nth-child(4) {
  animation-delay: 0.6s;
}

@keyframes th-login-pulse {
  0%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
}

.th-login-loading-text {
  text-align: center;
  color: #4a6cf7;
  font-size: 16px;
  font-weight: 500;
  margin-top: 10px;
}

/* ========== FINAL OPTIMIZED SELECT CONTROL ========== */
.modern-select-control {
  position: relative;
  margin-bottom: 24px;
    width:250px;
  font-family: 'Inter', system-ui, sans-serif;
  --border: rgba(140, 143, 148, 0.4);
  --border-hover: #4361ee;
  --background: #fff;
  --highlight: #f0f4ff;
  --selected: #e0e8ff;
}

.modern-select-trigger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px 14px;
  border: 1px solid var(--border);
  border-radius: 10px;
  background: var(--background);
  color: #2c3338;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 2;
}

.modern-select-trigger:hover {
  border-color: var(--border-hover);
}

.modern-select-dropdown {
  position: absolute;
  top: calc(100% + 5px);
  left: 0;
  right: 0;
  background: var(--background);
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, opacity 0.2s ease;
  opacity: 0;
  z-index: 10;
}

.modern-select-control.is-open .modern-select-dropdown {
  max-height: 220px;
  opacity: 1;
  overflow-y: auto;
}

.modern-select-option {
  padding: 10px 16px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.modern-select-option:hover {
  background: var(--highlight);
}

.modern-select-option.is-selected {
  background: var(--selected);
  font-weight: 500;
}

/* Scrollbar styling */
.modern-select-dropdown::-webkit-scrollbar {
  width: 6px;
}
.modern-select-dropdown::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,0.1);
  border-radius: 3px;
}

