.thl-form-fields-settings .field-drag-handle {
  margin-right: 12px;
  font-size: 18px;
  color: #94a3b8;
  cursor: grab;
  display: flex;
  align-items: center;
}

.thl-form-fields-settings .field-icon {
  margin-right: 10px;
  font-size: 18px;
  font-family: dashicons !important;
  color: #3b82f6;
}

.thl-form-fields-settings .field-label {
  font-size: 15px;
  font-weight: 500;
  color: #1e293b;
  flex-grow: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Action Icon (Lock / Delete) */
.thl-form-fields-settings .field-action-icon {
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  border-radius: 6px;
  transition: background 0.2s;

  .dashicons {
    font-size: 16px;
  }

  &.lock {
    color: #10b981;
    background-color: #ecfdf5;
  }

  &.delete {
    color: #ef4444;
    background-color: #fef2f2;
    cursor: pointer;

    &:hover {
      background-color: #fee2e2;
      color: #dc2626;
    }
  }
}

/* Add Field Button */
.thl-form-fields-settings .add-field-button-wrapper {
  padding-top: 16px;
  margin-top: auto;
  margin-left:auto;

  button {
    width: 100%;
    font-size: 14px;
    font-weight: 500;
    padding: 10px;
    background-color: #f9fafb;
    color: #334155;
    border-radius: 8px;
    transition: all 0.2s ease;

    &:hover {
      background-color: #f1f5f9;
      border-color: #64748b;
      color: #1e293b;
      transform: scale(1.02);
    }
  }
}

/* Right Panel */
.thl-form-fields-settings .thl-right-panel {
  flex-grow: 1;
  border: 1px solid #e5e7eb;
  border-radius: 16px;
  padding: 36px;
  background-color: #ffffff;
  min-height: 500px;

  .placeholder {
    text-align: center;

    p {
      font-size: 16px;
      color: #6b7280;
      margin: 0;
    }
  }
}


.thl-right-panel {
  flex: 1;
  max-width: 600px;
  min-width: 360px;
  padding: 32px;
  background-color: #fff;
  border-left: 1px solid #eee;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.02);
  overflow: visible;
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;
}

/* Field Editor Grid Layout */
.field-editor {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px 24px;
}

/* Elements that span both columns */
.field-editor .field-editor-title,
.field-editor .components-toggle-control,
.field-editor .check-group,
.field-editor .min-max-group,
.field-editor .field-editor-actions {
  grid-column: span 2;
}

/* Title Styling */
.field-editor-title {
  font-size: 20px;
  font-weight: 600;
  color: #1e1e1e;
  margin-bottom: 12px;
  border-bottom: 1px solid #eee;
  padding-bottom: 6px;
}

/* Input Styling */
.field-editor .components-text-control__input,
.field-editor .components-select-control__input {
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  background: #f9f9f9;
  font-size: 14px;
  transition: border 0.2s ease, box-shadow 0.2s ease;
}

.field-editor .components-text-control__input:focus,
.field-editor .components-select-control__input:focus {
  border-color: #007cba;
  background: #fff;
  box-shadow: 0 0 0 1px #007cba;
  outline: none;
}

/* Toggle Switches Row */
.field-editor .toggle-row {
  display: flex;
  gap: 24px;
  margin-top: 10px;
}

.field-editor .components-toggle-control:last-of-type {
  margin-bottom: 8px;
}

/* Password Check Rules */
.check-group {
  padding: 16px 20px;
  background: #f9f9f9;
  border-radius: 8px;
  border: 1px solid #eaeaea;
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

/* Min/Max Inputs Row */
.min-max-group {
  display: flex;
  gap: 20px;
}

.min-max-group .components-base-control {
  flex: 1;
}

/* Actions (Save/Reset) */
.field-editor-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

/* Placeholder if no field is selected */
.placeholder {
  padding: 60px 20px;
  font-size: 16px;
  color: #999;
  text-align: center;
  font-style: italic;
  line-height: 1.6;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .field-editor {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .thl-right-panel {
    max-width: 100%;
    padding: 24px;
  }
}

.thl-icon-picker {
  position: relative;
  font-family: inherit;
}

.icon-picker-trigger {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
    margin-top:-2px;
  padding: 3px 14px;
  border: 1px solid #dcdcdc;
  background-color: #f9f9f9;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.25s ease;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.04);
}

.icon-picker-trigger:hover {
  border-color: #007cba;
  background-color: #f0f8ff;
  box-shadow: 0 2px 8px rgba(0, 124, 186, 0.1);
}

.selected-icon svg {
  width: 22px;
  height: 22px;
  display: block;
}

.icon-name {
  font-size: 13px;
  color: #222;
  flex-grow: 1;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.icon-caret {
  color: #888;
  font-size: 16px;
  transform: translateY(1px);
  transition: 0.2s ease;
}

.icon-picker-dropdown {
  position: absolute;
  top: calc(100%);
  left: 0;
  z-index: 99;
  background: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 10px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
  max-height: 240px;
  overflow-y: auto;
  padding: 12px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(36px, 1fr));
  gap: 10px;
  width: 100%;
  animation: fadeIn 0.2s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.icon-option {
  padding: 6px;
  border-radius: 6px;
  background: #f5f5f5;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s ease, transform 0.1s ease;
  position: relative;
}

.icon-option:hover {
  background-color: #e6f3ff;
  transform: scale(1.05);
}

.icon-option.active {
  background-color: #007cba;
  color: #fff;
}

.icon-option.active svg {
  fill: #fff;
}

.icon-option svg {
  width: 22px;
  height: 22px;
  transition: fill 0.2s ease;
}

.field-item .selected-icon{
    width:20px;
    margin-right:5px;
    display:flex;
    align-items: center;
    justify-content: center;
}

.thl-right-panel .field-editor .modern-select-trigger{
  padding: 6px 14px;
}